import React, { useEffect } from "react";
import { useLocation } from "react-router-dom";
import "./components-css/SearchPage.css";
import Cards from "./Cards";
import { useJsApiLoader, GoogleMap, Marker } from "@react-google-maps/api";

function Searchpage(props) {
  const { isLoaded } = useJsApiLoader({
    googleMapsApiKey: "AIzaSyAeT8dPpXe-q8NfhLFsgSze2BrA1-BBkKo",
  });

  useEffect(() => {
    window.scrollTo(5 , 0);
  }, []);

  const { state } = useLocation();

  let searchResult = state.map((items) => {
    return (
      <Cards
        cardImage={items.cardImage}
        cardTitle={items.cardTitle}
        cardDescriptionDistance={items.cardDescriptionDistance}
        cardDescriptionReservation={items.cardDescriptionReservation}
        cardPrice={items.cardPrice}
        cardRating={items.cardRating}
        key={items.cardID}
        colClass="col"
        cardLat={items.lat}
        cardLng={items.lng}
        cardInsideTitle={items.cardInsideTitle}
        cardInsideImageI={items.cardInsideImageI}
        cardInsideImageII={items.cardInsideImageII}
        cardInsideImageIII={items.cardInsideImageIII}
        cardInsideImageIV={items.cardInsideImageIV}
        cardInsideHost={items.cardInsideHost}
        cardInsideGuest={items.cardInsideGuest}
        cardInsideBedroom={items.cardInsideBedroom}
        cardInsideBed={items.cardInsideBed}
        cardInsideBath={items.cardInsideBath}
        cardInsideDescription={items.cardInsideDescription}
      />
    );
  });

  let mapPosition = state.map((items) => {
    return (
      <Marker
        key={items.cardID}
        position={{ lat: items.lat, lng: items.lng }}
        icon={{
          path: "M12.75 0l-2.25 2.25 2.25 2.25-5.25 6h-5.25l4.125 4.125-6.375 8.452v0.923h0.923l8.452-6.375 4.125 4.125v-5.25l6-5.25 2.25 2.25 2.25-2.25-11.25-11.25zM10.5 12.75l-1.5-1.5 5.25-5.25 1.5 1.5-5.25 5.25z",
          fillColor: "#ff5a5f",
          fillOpacity: 1.0,
          strokeWeight: 0,
          scale: 1,
        }}
      />
    );
  });

  return (
    <div className={`search-page ${props.classToggle}`}>
      <div className="search-results">
        {searchResult.length > 0 && (
          <div>
            <h4 className="sp-text">
              <strong>
                {searchResult.length === 1
                  ? `${searchResult.length} stay`
                  : `${searchResult.length} stays`}
              </strong>
            </h4>

            <div className="cards-common home-subsection row-cols-sr row row-cols-md-2 g-4">
              {searchResult}
            </div>
          </div>
        )}

        {searchResult.length < 1 && (
          <div className="no-sr sp-text">
            <h4>
              <strong>Your search did not match</strong>
            </h4>

            <p className="text-muted">
              Make sure that all words are spelled correctly
            </p>
          </div>
        )}
      </div>

      {isLoaded && (
        <div className="g-map">
          <GoogleMap
            center={
              mapPosition.length > 0
                ? {
                    lat: mapPosition[0].props.position.lat,
                    lng: mapPosition[0].props.position.lng,
                  }
                : { lat: 0, lng: 0 }
            }
            zoom={10}
            mapContainerClassName="g-map-style"
            options={{
              zoomControl: false,
              fullscreenControl: false,
            }}
          >
            {mapPosition}
          </GoogleMap>
        </div>
      )}
    </div>
  );
}

export default Searchpage;
