import React from "react";
import { useNavigate } from "react-router-dom";
import "./components-css/Cards.css";

function Cards(props) {
  const navigate = useNavigate();

  let cardInsidePageID = props.cardTitle.toLowerCase().replace(/[,._ ]+/g, "-");
  const handleInside = () => {
    navigate(`/rooms/${cardInsidePageID}`, {
      state: [
        props.cardImage,
        props.cardTitle,
        props.cardDescriptionReservation,
        props.cardPrice,
        props.cardRating,
        props.cardInsideTitle,
        props.cardInsideImageI,
        props.cardInsideImageII,
        props.cardInsideImageIII,
        props.cardInsideImageIV,
        props.cardInsideHost,
        props.cardInsideGuest,
        props.cardInsideBedroom,
        props.cardInsideBed,
        props.cardInsideBath,
        props.cardInsideDescription,
        props.cardLat,
        props.cardLng,
      ],
    });
  };

  const currencyFormatter = (amount) => {
    return (
      "$" +
      amount
        .toFixed(2)
        .replace(/\d(?=(\d{3})+\.)/g, "$&,")
        .split(".")[0]
    );
  };

  return (
    <section className={props.colClass}>
      <div className="cards h-100" onClick={handleInside}>
        <img src={props.cardImage} alt="" className="card-img card-img-top" />

        <div className="card-info card-body">
          <div className="other-info">
            <h6 className="card-title">
              <strong>{props.cardTitle}</strong>
            </h6>

            <p className="card-description card-text text-muted distance">
              {props.cardDescriptionDistance}
            </p>

            <p className="card-description card-text text-muted reservation">
              {props.cardDescriptionReservation}
            </p>

            <p className="card-price">
              <strong>{currencyFormatter(props.cardPrice)}</strong> per night
            </p>
          </div>

          <div className="review-info">
            <span>{props.cardRating}</span>
            <i className="bi bi-star-fill star-icon"></i>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Cards;
