import React, { useEffect } from "react";
import CardsData from "./CardsData";
import { useJsApiLoader, GoogleMap, Marker } from "@react-google-maps/api";
import "./components-css/ShowMap.css";

function ShowMap(props) {
  const { isLoaded } = useJsApiLoader({
    googleMapsApiKey: "AIzaSyAeT8dPpXe-q8NfhLFsgSze2BrA1-BBkKo",
  });

  useEffect(() => {
    window.scrollTo(5, 0);
  }, []);

  let mapPositions = CardsData.map((items) => {
    return (
      <Marker
        key={items.cardID}
        position={{ lat: items.lat, lng: items.lng }}
        icon={{
          path: "M12.75 0l-2.25 2.25 2.25 2.25-5.25 6h-5.25l4.125 4.125-6.375 8.452v0.923h0.923l8.452-6.375 4.125 4.125v-5.25l6-5.25 2.25 2.25 2.25-2.25-11.25-11.25zM10.5 12.75l-1.5-1.5 5.25-5.25 1.5 1.5-5.25 5.25z",
          fillColor: "#ff5a5f",
          fillOpacity: 1.0,
          strokeWeight: 0,
          scale: 1,
        }}
      />
    );
  });

  return (
    <div className={`show-map ${props.classToggle}`}>
      {isLoaded && (
        <div className="g-map-whole">
          <GoogleMap
            center={{ lat: 29.279595803332284, lng: 14.288609722305306 }}
            zoom={3}
            mapContainerClassName="g-map-style"
            options={{
              zoomControl: false,
              fullscreenControl: false,
            }}
          >
            {mapPositions}
          </GoogleMap>
        </div>
      )}
    </div>
  );
}

export default ShowMap;
