import React from "react";
import "./components-css/Header.css";
import SearchBar from "./SearchBar";
import { Link } from "react-router-dom";

function Header(props) {
  return (
    <div className="header">
      <nav className="navbar navbar-expand-lg bg-light">
        <div className="container-fluid">
          <Link className="navbar-brand header-left" to="/">
            <img
              className="navbar-logo"
              src={props.logoImage}
              alt={props.logoAlt}
            />
          </Link>

          <div className="search-area header-center">
            <SearchBar />
          </div>

          <div className="header-right d-flex">
            <div className="pe-2">Become a host</div>
            <i className="bi bi-globe ps-2"></i>
            <i className="bi bi-arrow-down ps-2"></i>
            <i
              className="bi bi-person-circle ps-2"
              style={{ fontSize: "1.5rem" }}
            ></i>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default Header;
