import React, { useEffect, useState } from "react";
import Banner from "./Banner";
import Cards from "./Cards";
import "./components-css/Home.css";
import CardsData from "./CardsData";
import { useNavigate } from "react-router-dom";
import CategorySection from "./CategorySection.js";

function Home(props) {
  const navigate = useNavigate();

  const [showScroll, setShowScroll] = useState(false);

  useEffect(() => {
    window.scrollTo(5, 0);
  }, []);

  let sortedList;
  if (props.sort) {
    sortedList = CardsData.slice().sort(
      (a, b) => b.cardInsideGuest - a.cardInsideGuest
    );
  } else {
    sortedList = CardsData;
  }

  let cardElements = sortedList.map((items) => {
    return (
      <Cards
        cardImage={items.cardImage}
        cardTitle={items.cardTitle}
        cardDescriptionDistance={items.cardDescriptionDistance}
        cardDescriptionReservation={items.cardDescriptionReservation}
        cardPrice={items.cardPrice}
        cardRating={items.cardRating}
        key={items.cardID}
        colClass="col"
        cardLat={items.lat}
        cardLng={items.lng}
        cardInsideTitle={items.cardInsideTitle}
        cardInsideImageI={items.cardInsideImageI}
        cardInsideImageII={items.cardInsideImageII}
        cardInsideImageIII={items.cardInsideImageIII}
        cardInsideImageIV={items.cardInsideImageIV}
        cardInsideHost={items.cardInsideHost}
        cardInsideGuest={items.cardInsideGuest}
        cardInsideBedroom={items.cardInsideBedroom}
        cardInsideBed={items.cardInsideBed}
        cardInsideBath={items.cardInsideBath}
        cardInsideDescription={items.cardInsideDescription}
      />
    );
  });

  window.addEventListener("scroll", () => {
    if (window.scrollY > 500) {
      setShowScroll(true);
    } else {
      setShowScroll(false);
    }
  });

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  const handleMap = () => {
    navigate("/map");
  };

  return (
    <div className={`home ${props.classToggle}`}>
      <CategorySection />

      <Banner />

      <div className="home-subsec cards-common row row-cols-1 row-cols-md-5 g-4">
        {cardElements.slice(props.start, props.end)}
      </div>

      <div className="sm-btn">
        <button className="banner-btn map-toggle-btn" onClick={handleMap}>
          Show Map
        </button>
      </div>

      {showScroll && (
        <div className="scroll-to-top" onClick={scrollToTop}>
          <button className="st-btn">
            <i className="bi bi-chevron-up"></i>
          </button>
        </div>
      )}
    </div>
  );
}

export default Home;
