import React, { useState, useRef, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./components-css/CategorySection.css";
import CategoryData from "./CategoryData.js";

function CategorySection() {
  const navigate = useNavigate();

  const [catState, setCatState] = useState(0);

  const [refSecUlWidth, setRefSecUlWidth] = useState(0);
  const refSecUl = useRef(null);

  const [refCatItemWidth, setRefCatItemWidth] = useState(0);
  const refCatItem = useRef(null);

  useEffect(() => {
    setRefSecUlWidth(refSecUl.current.offsetWidth);
    setRefCatItemWidth(refCatItem.current.offsetWidth);
  }, []);

  let categoryElements = CategoryData.map((items) => {
    return (
      <li ref={refCatItem} className="nav-item cat-item" key={items.categoryID}>
        <div
          className="nav-link category-subsec"
          onClick={() => {
            navigate(`/category/${items.categoryName.toLowerCase()}`);
          }}
        >
          <div className="category-image">
            <img src={items.categoryImage} alt="" />
          </div>

          <div className="category-name">
            <p className="m-0">
              {" "}
              <strong>{items.categoryName}</strong>{" "}
            </p>
          </div>
        </div>
      </li>
    );
  });

  let tarPos = refSecUlWidth - refCatItemWidth * categoryElements.length;
  let catNavPos = `${catState}px`;

  const catSlide = (direction) => {
    if (direction === "right") {
      if (catState > tarPos) {
        if (catState === 0) {
          setCatState(-(refCatItemWidth - (refSecUlWidth % refCatItemWidth)));
        } else {
          setCatState(catState - refCatItemWidth);
        }
      }
    } else {
      if (catState < 0) {
        if (Math.abs(catState % refCatItemWidth) !== 0) {
          setCatState(
            catState + (refCatItemWidth - (refSecUlWidth % refCatItemWidth))
          );
        } else {
          setCatState(catState + refCatItemWidth);
        }
      }
    }
  };

  return (
    <div className="category-section">
      <div className="container-fluid cat-sec-main">
        <button
          className="category-previous cat-btn"
          onClick={(e) => {
            catSlide("left");
          }}
        >
          <i className="bi bi-chevron-left"></i>{" "}
        </button>

        <div className="position-relative nav-ul-parent">
          <ul
            ref={refSecUl}
            className="nav navbar-nav category-sec-ul"
            style={{ left: catNavPos }}
          >
            {categoryElements}
          </ul>
        </div>

        <button
          className="category-next cat-btn"
          onClick={(e) => {
            catSlide("right");
          }}
        >
          <i className="bi bi-chevron-right"></i>{" "}
        </button>
      </div>
    </div>
  );
}

export default CategorySection;
