import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./components-css/SearchBar.css";
import cardsData from "./CardsData";

function SearchBar() {
  const navigate = useNavigate();

  const [filteredData, setFilteredData] = useState([]);
  const [searchPageID, setSearchPageID] = useState("no-val");

  const handleFilter = (e) => {
    let searchWord = e.target.value.toLowerCase();
    setSearchPageID(searchWord);

    const newFilter = cardsData.filter((val) => {
      return val.cardTitle.toLowerCase().includes(searchWord);
    });

    if (searchWord === "") {
      setFilteredData([]);
    } else {
      setFilteredData(newFilter);
    }
  };

  const handleResultEquiv = (i) => {
    if (i.key === "Enter") {
      i.preventDefault();
      handleResult();
    }
  };

  const handleResult = () => {
    navigate(`/search/${searchPageID}`, { state: filteredData });
    let searchTextVal = document.querySelector(".search-text");
    searchTextVal.value = "";
  };

  return (
    <div className="search-bar">
      <input
        type="text"
        className="search-text"
        placeholder="Search by name"
        onChange={handleFilter}
        onKeyDown={handleResultEquiv}
      />

      <i className="bi bi-search" onClick={handleResult}></i>
    </div>
  );
}

export default SearchBar;
