import React from "react";
import { Link } from "react-router-dom";
import "./components-css/Footer.css";

function Footer(props) {
  return (
    <div className={`footer fixed-bottom ${props.classToggle}`}>
      <hr className="footer-hr" />

      <p className="fp-left text-muted fp-left-cr">
        {" "}
        &copy; 2022 Airbnb, Inc.{" "}
        <span>
          {" "}
          <span>.</span> Privacy <span>.</span> Terms <span>.</span> Sitemap{" "}
          <span>.</span> Destinations{" "}
        </span>
      </p>

      <Link to="/" className="fp-left" style={{ display: "none" }}>
        Go to Home
      </Link>

      <p className="fp-right text-muted">This is a demo</p>
    </div>
  );
}

export default Footer;
