import React, { useEffect } from "react";
import { useLocation } from "react-router-dom";
import { useJsApiLoader, GoogleMap, Marker } from "@react-google-maps/api";
import "./components-css/CardInside.css";

function CardInside(props) {
  const { isLoaded } = useJsApiLoader({
    googleMapsApiKey: "AIzaSyAeT8dPpXe-q8NfhLFsgSze2BrA1-BBkKo",
  });

  useEffect(() => {
    window.scrollTo(5, 0);
  }, []);

  const { state } = useLocation();

  const currencyFormatter = (amount) => {
    return (
      "$" +
      amount
        .toFixed(2)
        .replace(/\d(?=(\d{3})+\.)/g, "$&,")
        .split(".")[0]
    );
  };

  let formattedCardPrice = currencyFormatter(state[3]);

  let resStartDate = state[2].split(" ")[1];
  let resStartMonth = state[2].split(" ")[0];
  let resEndDate;
  let resEndMonth;

  if (state[2].split(" ").length === 4) {
    resEndDate = state[2].split(" ")[3];
    resEndMonth = state[2].split(" ")[0];
  } else {
    resEndDate = state[2].split(" ")[4];
    resEndMonth = state[2].split(" ")[3];
  }

  let priceByNights = state[3] * (resEndDate - resStartDate);
  let serviceFree = Math.round((priceByNights * 12) / 100);

  let bedroomCount = [...Array(state[12])].map((n, m) => {
    return (
      <div key={m} className="card-inside-becount-details me-4">
        <span className="material-symbols-outlined">king_bed</span>

        <p>
          <strong>Bedroom</strong>
        </p>

        <p className="m-0">1 bed</p>
      </div>
    );
  });

  let reviewCount = false;
  if (state[4] === "New") {
    reviewCount = true;
  }

  let imageSlider = false;
  if (window.matchMedia("(max-width: 600px)").matches) {
    imageSlider = true;
  }

  let roundRating = 0;
  if (state[4] < 5) {
    roundRating = state[4];
  } else {
    roundRating = Math.round(state[4]);
  }

  return (
    <div className={`card-inside ${props.classToggle}`}>
      <div className="container card-inside-cont">
        <h2 className="card-inside-title">{state[5]}</h2>

        <p className="card-inside-location">{state[1]}</p>

        {!imageSlider && (
          <div className="card-inside-images">
            <img
              className="card-inside-image"
              id="card-inside-image-main"
              src={state[0]}
              alt=""
            />

            <img
              className="card-inside-image"
              id="card-inside-image-I"
              src={state[6]}
              alt=""
            />

            <img
              className="card-inside-image"
              id="card-inside-image-II"
              src={state[7]}
              alt=""
            />

            <img
              className="card-inside-image"
              id="card-inside-image-III"
              src={state[8]}
              alt=""
            />

            <img
              className="card-inside-image"
              id="card-inside-image-IV"
              src={state[9]}
              alt=""
            />
          </div>
        )}

        {imageSlider && (
          <div
            id="carouselExampleControls"
            className="carousel slide"
            data-bs-ride="carousel"
          >
            <div className="carousel-inner">
              <div className="carousel-item active">
                <img
                  src={state[0]}
                  className="d-block w-100 card-inside-image"
                  alt="..."
                />
              </div>

              <div className="carousel-item">
                <img
                  src={state[6]}
                  className="d-block w-100 card-inside-image"
                  alt="..."
                />
              </div>

              <div className="carousel-item">
                <img
                  src={state[7]}
                  className="d-block w-100 card-inside-image"
                  alt="..."
                />
              </div>

              <div className="carousel-item">
                <img
                  src={state[8]}
                  className="d-block w-100 card-inside-image"
                  alt="..."
                />
              </div>

              <div className="carousel-item">
                <img
                  src={state[9]}
                  className="d-block w-100 card-inside-image"
                  alt="..."
                />
              </div>
            </div>

            <button
              className="carousel-control-prev"
              type="button"
              data-bs-target="#carouselExampleControls"
              data-bs-slide="prev"
            >
              <span
                className="carousel-control-prev-icon"
                aria-hidden="true"
              ></span>

              <span className="visually-hidden">Previous</span>
            </button>

            <button
              className="carousel-control-next"
              type="button"
              data-bs-target="#carouselExampleControls"
              data-bs-slide="next"
            >
              <span
                className="carousel-control-next-icon"
                aria-hidden="true"
              ></span>

              <span className="visually-hidden">Next</span>
            </button>
          </div>
        )}

        <div className="card-inside-more-details mt-5">
          <div className="card-inside-checkout-tab">
            <div className="card-inside-price">
              <h5>
                <strong>{formattedCardPrice}</strong>
              </h5>

              <p className="ps-1" style={{ marginTop: "1px" }}>
                night
              </p>
            </div>

            <div className="card-inside-dates">
              <div className="card-inside-dates-ci cog">
                <small>
                  <strong>CHECK-IN</strong>
                </small>

                <p>{resStartDate + "/" + resStartMonth}</p>
              </div>

              <div className="card-inside-dates-co cog">
                <small>
                  {" "}
                  <strong>CHECK-OUT</strong>{" "}
                </small>

                <p>{resEndDate + "/" + resEndMonth}</p>
              </div>

              <div className="card-inside-guest-sec cog">
                <p>
                  <small>
                    <label htmlFor="card-inside-guest-count">
                      <strong>GUESTS</strong>
                    </label>
                  </small>
                </p>

                <input
                  type="number"
                  name="card-inside-guest-count"
                  id="card-inside-guest-count"
                  min={0}
                  defaultValue={2}
                />
              </div>
            </div>

            <button className="card-inside-reserve-btn banner-btn">
              Reserve
            </button>

            <p className="text-center m-2 text-muted">
              You won't be charged Yet
            </p>

            <div className="card-inside-payment-calc mt-4">
              <div className="price-by-nights d-flex justify-content-between">
                <p>
                  <u>
                    {formattedCardPrice} X {resEndDate - resStartDate} nights
                  </u>
                </p>

                <p>{currencyFormatter(priceByNights)}</p>
              </div>

              <div className="service-fee d-flex justify-content-between">
                <p>
                  <u>Service fee</u>
                </p>

                <p>{currencyFormatter(serviceFree)}</p>
              </div>

              <hr />

              <div className="total-payment d-flex justify-content-between">
                <p>
                  <strong>Total before taxes</strong>
                </p>

                <p>
                  <strong>
                    {currencyFormatter(priceByNights + serviceFree)}
                  </strong>
                </p>
              </div>
            </div>
          </div>

          <div className="card-inside-others">
            <div className="card-inside-host">
              <h4>
                {" "}
                <strong> Dome hosted by {state[10]}</strong>
              </h4>

              <p>
                {" "}
                {state[11]} guests <span>.</span> {state[12]} bedroom{" "}
                <span>.</span> {state[13]} bed <span>.</span> {state[14]} bath
              </p>
            </div>

            <hr className="custom-my" />

            <div className="card-inside-about">
              <p>{state[15]}</p>
            </div>

            <hr className="custom-my" />

            <div className="card-inside-bedcount">
              <h4 className="card-inside-bedcount-heading">
                <strong>Where you'll sleep</strong>
              </h4>

              <div className="bedcount-cont d-flex flex-wrap">
                {bedroomCount}
              </div>
            </div>

            <hr className="custom-my" />

            <div className="card-inside-review-sec">
              <h4>
                <strong>Ratings for this place</strong>
              </h4>

              {reviewCount
                ? `No reviews (yet)`
                : `${roundRating} stars out of 5`}
            </div>
          </div>
        </div>

        {isLoaded && (
          <div className="card-inside-g-map">
            <GoogleMap
              center={{ lat: state[16], lng: state[17] }}
              zoom={15}
              mapContainerClassName="g-map-style"
              options={{
                zoomControl: false,
                fullscreenControl: false,
              }}
            >
              <Marker
                position={{ lat: state[16], lng: state[17] }}
                icon={{
                  path: "M12.75 0l-2.25 2.25 2.25 2.25-5.25 6h-5.25l4.125 4.125-6.375 8.452v0.923h0.923l8.452-6.375 4.125 4.125v-5.25l6-5.25 2.25 2.25 2.25-2.25-11.25-11.25zM10.5 12.75l-1.5-1.5 5.25-5.25 1.5 1.5-5.25 5.25z",
                  fillColor: "#ff5a5f",
                  fillOpacity: 1.0,
                  strokeWeight: 0,
                  scale: 1,
                }}
              />
            </GoogleMap>
          </div>
        )}
      </div>
    </div>
  );
}

export default CardInside;
