import { useState } from "react";
import { HashRouter as Router, Routes, Route } from "react-router-dom";
import "./App.css";
import Footer from "./components/Footer";
import Header from "./components/Header";
import Home from "./components/Home";
import SearchDatePicker from "./components/SearchDatePicker.js";
import SearchPage from "./components/SearchPage.js";
import ShowMap from "./components/ShowMap.js";
import CardInside from "./components/CardInside.js";
import airbnb_logo from "./media/airbnb-logo.svg";

function App() {
  const [showSearch, setShowSearch] = useState(false);

  const toggleSearch = () => {
    setShowSearch(!showSearch);
  };

  document.addEventListener("click", (e) => {
    if (
      showSearch &&
      !e.target.closest(".search-date-btn") &&
      !e.target.closest(".search-date-picker-sub-cont")
    ) {
      setShowSearch(!showSearch);
    }
  });

  return (
    <div className="app">
      <Router>
        <div className="h-sd sticky-top">
          <Header logoImage={airbnb_logo} logoAlt="Airbnb" />

          <div className="search-date">
            <button className="search-date-btn" onClick={toggleSearch}>
              {showSearch ? "Hide" : "Search Dates"}
            </button>
            {showSearch && <SearchDatePicker />}
          </div>
        </div>

        <Routes>
          <Route
            path="/search/:searchPageID"
            element={
              <SearchPage classToggle={showSearch ? "unfocused-bg" : ""} />
            }
          />

          <Route
            path="/map"
            element={<ShowMap classToggle={showSearch ? "unfocused-bg" : ""} />}
          />

          <Route
            path="/rooms/:cardTitle"
            element={
              <CardInside classToggle={showSearch ? "unfocused-bg" : ""} />
            }
          />

          <Route
            path="category/arctic"
            element={
              <Home
                classToggle={showSearch ? "unfocused-bg" : ""}
                start={20}
                end={31}
              />
            }
          />

          <Route
            path="category/islands"
            element={
              <Home
                classToggle={showSearch ? "unfocused-bg" : ""}
                start={0}
                end={20}
              />
            }
          />

          <Route
            path="/"
            element={
              <Home
                classToggle={showSearch ? "unfocused-bg" : ""}
                sort={true}
              />
            }
          />
        </Routes>

        <Footer
          classToggle={showSearch ? "unfocused-bg" : ""}
          logoImage={airbnb_logo}
          logoAlt="Airbnb"
        />
      </Router>
    </div>
  );
}

export default App;
