import React, { useState } from "react";
import "./components-css/SearchDatePicker.css";
import { DateRangePicker } from "react-date-range";
import { useNavigate } from "react-router-dom";
import cardsData from "./CardsData";
import "react-date-range/dist/styles.css"; // main style file
import "react-date-range/dist/theme/default.css"; // theme css file

function SearchDate() {
  const navigate = useNavigate();

  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const [searchPageID, setSearchPageID] = useState("no-val");
  const [filteredData, setFilteredData] = useState([]);

  const selectionRange = {
    startDate: startDate,
    endDate: endDate,
    key: "selection",
  };

  function dateSelect(ranges) {
    setStartDate(ranges.selection.startDate);
    setEndDate(ranges.selection.endDate);

    let searchWord = startDate.toDateString().toLowerCase().split(" ")[1];
    setSearchPageID(searchWord);

    const newFilter = cardsData.filter((val) => {
      return val.cardDescriptionReservation.toLowerCase().includes(searchWord);
    });

    setFilteredData(newFilter);
  }

  const handleResult = () => {
    navigate(`/search/${searchPageID}`, { state: filteredData });
  };

  return (
    <div className="search-date-picker">
      <div className="search-date-picker-cont">
        <div className="search-date-picker-sub-cont">
          <DateRangePicker
            className="date-range-picker"
            rangeColors={["#ff5a5f"]}
            ranges={[selectionRange]}
            onChange={dateSelect}
          />

          <div className="guest-section-search">
            <label className="guest-count-label" htmlFor="guest-count">
              <p className="fw-bold">Number of guests</p>
              <i className="bi bi-people-fill"></i>
            </label>

            <input
              type="number"
              name="guest-count"
              id="guest-count"
              min={0}
              defaultValue={2}
            />
          </div>
        </div>

        <button className="date-pick-btn banner-btn" onClick={handleResult}>
          Search Airbnb
        </button>
      </div>
    </div>
  );
}

export default SearchDate;
